import React from 'react';
import './Home.css';
import manOnTopMountain from '../img/man-stands-top-mountain.jpg';
import step1 from '../img/how-works-1.png'
import step2 from '../img/how-works-2.png'
import step3 from '../img/how-works-3.png'


class Home extends React.Component {

    render() {
        return (
            <div className="home">
                <div className="container">
                    <div className="intro">
                        <div className="article">
                            <p className="article__title" > Be succesful </p>
                            <p className="article__subtitle" > Stay focused on your live, build powerful habits and achive your goals </p>
                        </div>
                        < a className="intro__button_blue" href="#started"> Get Started </a>
                        <div className="intro__img block-img" >
                            <img className="block-img__img" src={manOnTopMountain} alt="Загрузка" />
                        </div>
                    </div>
                    <div className="instruction" >
                        <div className="article" >
                            <p className="article__title" > How it works ? </p>
                            <p className="article__subtitle" > Habit building in its essential steps : create a habit, actually < br /> remember to do it, and track your development. </p>
                        </div>
                        <div className="row">
                            <div className="columns-3" >
                                <div className="columns-3__column" >
                                    <div className="block-img_small" >
                                        <img className="block-img__img" src={step1} alt="Загрузка" />
                                    </div>
                                    <div className="article" >
                                        <p className="article__title_small" > 1. Set up your habits </p>
                                        <p className="article__subtitle_small" > Set a list of habits to create your daily routiner and start your journey. </p>
                                    </div>
                                </div>
                                <div className="columns-3__column" >
                                    <div className="block-img_small" >
                                        <img className="block-img__img" src={step2} alt="Загрузка" />
                                    </div>
                                    <div className="article" >
                                        <p className="article__title_small" > 2. Review your habits daily </p>
                                        <p className="article__subtitle_small" > See your habits for the day. Don`t forget do and checked this one. </p>
                                    </div>
                                </div>
                                <div className="columns-3__column" >
                                    <div className="block-img_small" >
                                        <img className="block-img__img" src={step3} alt="Загрузка" />
                                    </div>
                                    <div className="article" id='started'>
                                        <p className="article__title_small" > 3. See your progress </p>
                                        <p className="article__subtitle_small" > View your habit activity through the weeks and mounths. Don`t give up achive your goals.</p>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="article" >
                            <p className="article__title" > How started? </p>
                            <p className="article__subtitle" >It`s very easy and fast just do this several steps:</p>
                        </div>
                        <div className="row">
                            <div className="columns-3" >
                                <div className="columns-3__column" >
                                    <div className="article" >
                                        <p className="article__title_small" > 1. Sign In or Sign up</p>
                                        <p className="article__subtitle_small" >We can't traking your activity without your account</p>
                                    </div>
                                </div>
                                <div className="columns-3__column" >
                                    <div className="article" >
                                        <p className="article__title_small" > 2. Click on menu Habits</p>
                                        <p className="article__subtitle_small" >This page will contain your Habits</p>
                                    </div>
                                </div>
                                <div className="columns-3__column" >
                                    <div className="article" >
                                        <p className="article__title_small" > 3. Create new habit</p>
                                        <p className="article__subtitle_small" > Just click "Create new habit", give name and frequency of your new habits</p>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default Home;