import React from 'react';
import './Habits.css';
import OneHabit from './OneHabit';


class Habits extends React.Component {

    showHabitInfo = (target) => {
        const habitBlock = target.target.parentElement.parentElement;
        const buttonShow = habitBlock.children[0].children[2];
        const habitInfo = habitBlock.children[1];
        if (habitInfo.classList.length === 2) {
            habitInfo.classList.remove('hide');
            buttonShow.classList.add('active');
        }
        else {
            habitInfo.classList.add('hide');
            buttonShow.classList.remove('active');
        }
    }

    showHabitForm = () => {
        document.querySelector('.habit-form').classList.toggle('hide');
        document.querySelector('.modal-wrap').classList.toggle('hide');
    }

    closeModalWindow = () => {
        document.querySelector('.modal-wrap').classList.add('hide');
        document.querySelector('.modal-window').classList.add('hide');
    }

    render() {
        const daylyHabits = [];
        const weeklyHabits = [];
        if (this.props.state.signIn) {
            for (let key in this.props.state.userData.daylyHabits) {
                daylyHabits.push(<OneHabit dayly={true} showHabitInfo={this.showHabitInfo} name={key} key={key.toString()} habit={this.props.state.userData.daylyHabits[key]} setDeleteHabit={this.props.setDeleteHabit} updateHabitBook={this.props.updateHabitBook} />)
            }
            for (let key in this.props.state.userData.weeklyHabits) {
                weeklyHabits.push(<OneHabit dayly={false} showHabitInfo={this.showHabitInfo} name={key} key={key.toString()} habit={this.props.state.userData.weeklyHabits[key]} setDeleteHabit={this.props.setDeleteHabit} updateHabitBook={this.props.updateHabitBook} />)
            }
        }
        return (this.props.state.signIn) ? (
            <div className="habits">
                <div className="container">
                    <div className="habits-block daily-habits">
                        <p className="habits-block__title">This is your daily habits</p>
                        <button className="habits-block__button_blue" onClick={this.showHabitForm}>Create new habit</button>
                        {daylyHabits}
                    </div>
                    <div className="habits-block weekly-habits">
                        <p className="habits-block__title ">This is your weekly habits</p>
                        {weeklyHabits}
                    </div>
                </div>
                <form className="habit-form hide" onSubmit={this.props.createHabit}>
                    <span className="close_black" onClick={this.showHabitForm}></span>
                    <div className="form__group">
                        <input type="text" name="input-name" className="form__input" placeholder=" " value={this.props.state.newHabit.name} onChange={this.props.setHabitName} />
                        <label htmlFor="input-name" className="form__label">Name</label>
                        <p className="form__message hide">*Заповініть це поле</p>
                    </div>
                    <p className="form__title">Frequency:</p>
                    <div className="form__group_center">
                        <input type="radio" name="frequency" id="frequency-dayly" defaultChecked className="form__input_radio"
                            onClick={this.props.setHabitFrequency} />
                        <label htmlFor="frequency-dayly" className="form__label_radio">Everyday</label>
                    </div>
                    <div className="form__group_center">
                        <input type="radio" name="frequency" id="frequency-weekly" className="form__input_radio" onClick={this.props.setHabitFrequency} />
                        <label htmlFor="frequency-weekly" className="form__label-radio">Everyweek</label>
                    </div>
                    <input type="submit" className="form__submit_blue" value="Create" />
                </form>
                <div>
                    <div className="modal-wrap hide"></div>
                    <div className="modal-window hide">
                        <div className="close_white" onClick={this.closeModalWindow}></div>
                        <p className="modal-window__message">This habit will be deleted forever</p>
                        <button className="modal-window__button" onClick={this.props.deleteHabit}>OK</button>
                    </div>
                </div>
            </div>
        ) :
            (
                <div className="habits">
                    <div className="container">
                        <div className="habits-block daily-habits">
                            <p className="habits-block__title">This is your daily habits</p>
                            <button className="habits-block__button_blue">Create new habit</button>
                            <div className="habit">
                                <div className="habit__main">
                                    <input type="checkbox" className="habit__check" />
                                    < p className="habit__name">Read 20 page</p>
                                    < span className="habit__info"></span>
                                </div>
                            </div>
                            <div className="habit">
                                <div className="habit__main">
                                    <input type="checkbox" className="habit__check" />
                                    < p className="habit__name">Run 3 km</p>
                                    < span className="habit__info"></span>
                                </div>
                            </div>
                        </div>
                        <div className="habits-block weekly-habits">
                            <p className="habits-block__title ">This is your weekly habits</p>
                            <div className="habit">
                                <div className="habit__main">
                                    <input type="checkbox" className="habit__check" />
                                    < p className="habit__name">Read 140 page</p>
                                    < span className="habit__info"></span>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div>
                        <div className="modal-wrap"></div>
                        <div className="modal-window">
                            <p className="modal-window__message">You must be login</p>
                        </div>
                    </div>
                </div>
            )
    }
}

export default Habits;
