import React from 'react';

class OneHabit extends React.Component {
    render() {
        const name = this.props.name;
        let rowBlocks = [];
        const historyRows = [];
        let keyCount = 0;
        const history = this.props.habit.history;
        let inputCheck = 0;
        if (history[0] === true) {
            inputCheck = <input type="checkbox" className="habit__check" defaultChecked onClick={this.props.updateHabitBook} data-name={name.toString()} data-dayly={this.props.dayly.toString()} data-key={'0'} />
        }
        else {
            inputCheck = <input type="checkbox" className="habit__check" onClick={this.props.updateHabitBook} data-name={name.toString()} data-dayly={this.props.dayly.toString()} data-key={'0'} />
        }
        // створення блоку історії, проходить цикл 7 раз , пушить в масив блоки , на 7 раз створює наступний рядок
        for (let key in history) {
            if (+key === 0 && this.props.dayly) {
                if (this.props.habit.history[key] === true) {
                    rowBlocks.push(<div key={key.toString()} data-name={name.toString()} data-dayly={'true'} data-key={key.toString()} onClick={this.props.updateHabitBook} className="history__block_check"><p className="history__block_today">today</p></div>);
                }
                else {
                    rowBlocks.push(<div key={key.toString()} data-name={name.toString()} data-dayly={'true'} data-key={key.toString()} onClick={this.props.updateHabitBook} className="history__block_uncheck"><p className="history__block_today">today</p></div>);
                }
                continue;
            }
            if (+key === 0 && !this.props.dayly) {
                if (this.props.habit.history[key] === true) {
                    rowBlocks.push(<div key={key.toString()} className="history__block_check" data-name={name.toString()} data-dayly={'false'} data-key={key.toString()}><p className="history__block_today" style={{ top: '-30px' }}>this week</p></div>);
                }
                else {
                    rowBlocks.push(<div key={key.toString()} data-name={name.toString()} data-dayly={'false'} data-key={key.toString()} className="history__block_uncheck"><p className="history__block_today" style={{ top: '-30px' }}>this week</p></div>);
                }
                continue;
            }
            if (this.props.dayly) {
                if (history[key] === true) {
                    rowBlocks.push(<div key={key.toString()} data-name={name.toString()} data-dayly={'true'} data-key={key.toString()} className="history__block_check" onClick={this.props.updateHabitBook}></div>);
                }
                else {
                    rowBlocks.push(<div key={key.toString()} data-name={name.toString()} data-dayly={'true'} data-key={key.toString()} className="history__block_uncheck" onClick={this.props.updateHabitBook}></div>);
                }
            } else {
                if (history[key] === true) {
                    rowBlocks.push(<div key={key.toString()} data-name={name.toString()} data-dayly={'false'} data-key={key.toString()} className="history__block_check" onClick={this.props.updateHabitBook}></div>);
                }
                else {
                    rowBlocks.push(<div key={key.toString()} data-name={name.toString()} data-dayly={'false'} data-key={key.toString()} className="history__block_uncheck" onClick={this.props.updateHabitBook}></div>);
                }
            }

            if ((Number(key) + 1) % 7 === 0 || Number(key) === 30) {
                const historyOneRow = <div key={keyCount.toString()} className="history-rows__row">{rowBlocks}</div>
                keyCount += 1;
                historyRows.push(historyOneRow);
                rowBlocks = [];
            }
        }

        let activityColumns = [];
        let activityCount = 0;
        let arr = Object.values(history);
        let activityArr = arr.reverse().filter((elem, index) => (index !== 31 && index > 18));
        activityArr.map((elem, index) => {
            if (elem === true) {
                activityCount === 5 ? activityCount = 5 : activityCount += 1;
                if (index > 4) {
                    activityColumns.push(<div className="history__column" key={index.toString()} style={{ height: `${1 * activityCount * 20}%` }}></div>)
                }
            }
            else {
                activityCount = 0;
                if (index > 4) {
                    activityColumns.push(<div className="history__column" key={index.toString()} style={{ height: `${1 * activityCount * 20}%` }}></div>)
                }
            }
        })

        return (this.props.dayly) ? (
            <div className="habit" >
                <div className="habit__main">
                    {inputCheck}
                    < p className="habit__name">{name}</p>
                    < span className="habit__info-button" onClick={this.props.showHabitInfo}></span>
                </div>
                <div className="habit__info hide">
                    <div className="history">
                        <div className="history__title"><div className="history__block_check"></div><div className="history__block_uncheck"></div> - check / uncheck<span>Last 30 days</span></div>
                        <div className="history-rows">
                            {historyRows}
                        </div>
                        <button className="habits-block__button_blue_small" data-name={name} data-frequency={'dayly'} onClick={this.props.setDeleteHabit}>Delete habit</button>
                    </div>
                    <div className="history__activity">
                        <p className="history__title">Activity last 7 days</p>
                        <div className="history__diagram">
                            {activityColumns}
                        </div>
                    </div>
                </div>
            </div>
        ) :
            (
                <div className="habit" >
                    <div className="habit__main">
                        {inputCheck}
                        < p className="habit__name">{name}</p>
                        < span className="habit__info-button" onClick={this.props.showHabitInfo}></span>
                    </div>
                    <div className="habit__info hide">
                        <div className="history">
                            <div className="history__title"><div className="history__block_check"></div><div className="history__block_uncheck"></div> - check / uncheck<span>Last 30 weeks</span></div>
                            <div className="history-rows">
                                {historyRows}
                            </div>
                            <button className="habits-block__button_blue_small" data-name={name} data-frequency={'weekly'} onClick={this.props.setDeleteHabit}>Delete habit</button>
                        </div>
                        <div className="history__activity">
                            <p className="history__title">Activity last 7 weeks</p>
                            <div className="history__diagram">
                                {activityColumns}
                            </div>
                        </div>
                    </div>
                </div>
            )
    }
}

export default OneHabit;


