import React from 'react';
import './Authorisation.css';
import { auth } from '../index';
import { database } from '../index';
import avatar_icon from '../img/user-icon.png';
import arrow_right from '../img/arrow-right.png';
import eye_icon from '../img/eye-black.png';

class Authorisation extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            showRegistration: this.props.state.showRegistration,
            email: '',
            password: '',
            repassword: '',
            name: '',
            uid: '',
        }
    }

    writeName = ({ target: { value } }) => {
        this.setState({ name: value.replace(/[^a-zA-Z0-9а-яА-Я]/g, '') });
    }
    writeEmail = ({ target: { value } }) => {
        this.setState({ email: value.replace(/[^a-zA-Z0-9@.]/g, '') });
    }
    writePassword = ({ target: { value } }) => {
        this.setState({ password: value.replace(/[^a-zA-Z0-9]/g, '') });
    }
    writeRePassword = ({ target: { value } }) => {
        this.setState({ repassword: value.replace(/[^a-zA-Z0-9]/g, '') });
    }

    writeUserEmail = (event) => {
        if (event.key.search(/[a-zA-Z0-9@.]/) !== -1 || event.key === 'Backspace') {
            setTimeout(() => {
                this.setState({ email: event.target.value });
            }, 300);
            return true;
        }
        event.preventDefault();
    }

    showErrorMessage = (message, time = 3000) => {
        let validationMessageBlock = document.querySelector('.validation-message');
        validationMessageBlock.classList.remove('hide');
        validationMessageBlock.classList.add('active');
        let validationMessage = validationMessageBlock.firstChild;
        validationMessage.innerHTML = message;
        setTimeout(() => {
            validationMessageBlock.classList.add('hide');
            validationMessageBlock.classList.remove('active');
        }, time);
    }

    validateEmailAndPassword = () => {
        if (!this.state.password || !this.state.email) {
            return [false, 'Please enter all inputs']
        }
        if (this.state.password.length < 6 || this.state.email.length < 6) {
            return [false, 'Password and email must contain more than 6 symbols'];
        }
        return [true];
    }

    validationSignUp = () => {
        if (!this.state.name) {
            return [false, 'Please enter name'];
        }
        if (!this.state.password || !this.state.email || !this.state.repassword) {
            return [false, 'Please enter all inputs']
        }
        if (this.state.password !== this.state.repassword) {
            return [false, 'Passwords don`t equal'];
        }
        if (!this.validateEmailAndPassword()[0]) {
            return this.validateEmailAndPassword();
        }
        return [true];
    }

    signIn = (event) => {
        if (this.validateEmailAndPassword()[0]) {
            auth.signInWithEmailAndPassword(this.state.email, this.state.password)
                .then(user => {
                    this.props.setUid(user.user.uid, user.user.displayName);
                })
                .catch((error) => {
                    this.showErrorMessage(error.message, 3000);
                })
        }
        else {
            this.showErrorMessage(this.validateEmailAndPassword()[1]);
        }
        event.preventDefault();
    }

    createAccount = (event) => {
        if (this.validationSignUp()[0]) {
            auth.createUserWithEmailAndPassword(this.state.email, this.state.password)
                .then(dataUser => {
                    dataUser.user.updateProfile({
                        displayName: this.state.name,
                    });
                    database.ref(`users/${dataUser.user.uid}`).set({
                        'email': `${this.state.email}`,
                        'name': `${this.state.name}`
                    });
                    this.props.signIn();
                    this.props.setUid(dataUser.user.uid, this.state.name);
                })
                .catch(error => {
                    this.showErrorMessage(error.message, 2000);
                });
        }
        else {
            this.showErrorMessage(this.validationSignUp()[1]);
        }
        event.preventDefault();
    }

    switchRegistration = () => {
        this.setState({ showRegistration: !this.state.showRegistration });
    }

    showPassword = () => {
        let input = document.querySelector('input[type=password]');
        if (input) {
            input.type = 'text';
            setTimeout(() => {
                input.type = 'password';
            }, 2000);
        }
    }

    render() {
        return !this.state.showRegistration ? (
            <div className="authorisation">
                <form className="sign-in" onSubmit={this.signIn}>
                    <div className="imgcontainer">
                        <img src={avatar_icon} alt="avatar" className="avatar" />
                    </div>
                    <label><b>Email</b></label>
                    <input type="text" className='input-email' name="email" placeholder="Enter email" onChange={this.writeEmail} value={this.state.email} />
                    <label><b>Password</b></label>
                    <div className="group">
                        <input type="password" className='input-password' name="psw" placeholder="Enter password" onChange={this.writePassword} value={this.state.password} />
                        <img src={eye_icon} alt='show_pass' className="show-pass-img" onClick={this.showPassword} />
                    </div>
                    <button type="submit">Login</button>
                    <div className="switch-link">
                        <p>Don`t have account?</p>
                        <p onClick={this.switchRegistration}>Registration<img src={arrow_right} alt="arrow" /></p>
                    </div>
                    <div className='validation-message hide'>
                        <p></p>
                    </div>
                </form>
            </div>
        ) : (
            <div className="authorisation">
                < form className="sign-up" onSubmit={this.createAccount}>
                    <h4>Registration</h4>
                    <input type="text" className='input-name' placeholder="Enter name" onChange={this.writeName} value={this.state.name} />
                    <input type="text" className='input-email' placeholder="Enter email" onChange={this.writeEmail} value={this.state.email} />
                    <div className="group">
                        <input type="password" className='input-password' placeholder="Enter password" onChange={this.writePassword} value={this.state.password} />
                        <img src={eye_icon} alt='show_pass' className="show-pass-img" onClick={this.showPassword} />
                    </div>
                    <input type="password" className='input-password' id='re-password' placeholder="Enter re-password" onChange={this.writeRePassword} value={this.state.repassword} />
                    <button type="submit">Sign up</button>
                    <div className="switch-link">
                        <p>Do you have account?</p>
                        <p onClick={this.switchRegistration}>Login<img src={arrow_right} alt="arrow" /></p>
                    </div>
                    <div className='validation-message hide'>
                        <p></p>
                    </div>
                </form >
            </div>
        )
    }
}

export default Authorisation;