import React from 'react';
import './Footer.css';
import fsIcon from './fs-icon.png';
import twitterIcon from './twitter-icon.png';
import instagramIcon from './instagram-icon.png';

class Footer extends React.Component {

    render() {

        return (
            <div className="footer">
                <div className="container">
                    <div className="columns-3">
                        <div className="columns-3__column">
                            <p className="header__logo_white">MyHabits</p>
                            <p className="footer__info">Privacy Policy</p>
                            <p className="footer__info">Terms of use</p>
                        </div>
                        <div className="columns-3__column">
                            <div className="footer__social-media">
                                <div className="block-img">
                                    < img className="block-img__img" src={fsIcon} alt="Загрузка" />
                                </div>
                                <div className="block-img">
                                    < img className="block-img__img" src={twitterIcon} alt="Загрузка" />
                                </div>
                                <div className="block-img">
                                    < img className="block-img__img" src={instagramIcon} alt="Загрузка" />
                                </div>
                            </div>
                        </div>
                        <div className="columns-3__column">
                            <p className="footer__info_title_right">Resources</p>
                            <a className="footer__info_right" href="mailto:mr.bravespirit@gmail.com?subject=website MyHabits">Email</a>
                            <p className="footer__info_right" onClick={this.props.signIn}>Sign In</p>
                            <p className="footer__info_right" onClick={this.props.signUp}>Sign Up</p>
                        </div>
                    </div>
                </div>
            </div>
        )
    };
}

export default Footer;