import React from 'react';
import {
    HashRouter as Router,
    Switch,
    Route,
    Link
} from "react-router-dom";
import './Header.css';
import Home from '../home/Home';
import Habits from '../habits/Habits';

class Header extends React.Component {

    toggleMenu = () => {
        document.querySelector('.header__burger').classList.toggle('active');
        document.querySelector('.header__menu').classList.toggle('active');
    }

    render() {
        return (
            <Router >
                <div className="header">
                    <div className="container">
                        <div className="header__body">
                            <div className="header__logo_black">
                                <p>MyHabits</p>
                            </div>
                            <nav className="header__menu">
                                <ul className="header__list">
                                    <li>
                                        <Link className="header__link" to="/">Home</Link>
                                    </li>
                                    <li>
                                        <Link className="header__link" to="/habits">Habits</Link>
                                    </li>
                                    <li className="header__sign-in">
                                        < p className="header__link" onClick={this.props.signIn}>{this.props.state.userData.name}</p>
                                    </li>
                                </ul>
                            </nav>
                            <div className="header__burger" onClick={this.toggleMenu}>
                                <span></span>
                            </div>
                        </div>
                    </div>
                </div>
                <Switch>
                    <Route path="/habits">
                        <Habits signIn={this.props.state.signIn} state={this.props.state} setHabitFrequency={this.props.setHabitFrequency} setHabitName={this.props.setHabitName} createHabit={this.props.createHabit} deleteHabit={this.props.deleteHabit} setDeleteHabit={this.props.setDeleteHabit} updateHabitBook={this.props.updateHabitBook} />
                    </Route>
                    <Route path="/">
                        <Home />
                    </Route>
                </Switch>
            </Router>
        )
    };
}

export default Header;



