import './App.css';
import React from 'react';
import Header from './header/Header';
import Footer from './footer/Footer'
import Authorisation from './authorisation/Authorisation';
import { database } from './index';

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      signIn: false,
      uid: '',
      showRegistration: false,
      userData: {
        name: 'Sign In'
      },
      newHabit: {
        name: '',
        frequency: 'dayly',
      },
      deleteHabit: {
        name: '',
        frequency: ''
      }
    }
  }

  signIn = () => {
    if (!this.state.signIn) {
      this.setState({ signIn: true });
    }
  }

  signUp = () => {
    if (!this.state.signIn) {
      this.setState({ showRegistration: !this.state.showRegistration });
    }
  }

  getUserData = async (uid, update = false) => {
    let data = await fetch(`https://myhabits-app-default-rtdb.firebaseio.com/users/${uid}.json`)
      .then(response => response.json());
    if (update) {
      if (data.daylyHabits) {
        let daylyHabitsValue = Object.values(data.daylyHabits);
        daylyHabitsValue.forEach(habit => {
          let dataUpdate = this.updateHabitHistory(habit, true);
          data.daylyHabits[habit.name] = dataUpdate;
        });
      }
      if (data.weeklyHabits) {
        let weeklyHabits = Object.values(data.weeklyHabits);
        weeklyHabits.forEach(habit => {
          let dataUpdate = this.updateHabitHistory(habit, false);
          data.weeklyHabits[habit.name] = dataUpdate;
        });
      }
    }
    this.setState({
      userData: data,
    });
  }

  updateHabitHistory = (habit, daylyFrequency) => {
    // this func for know last activity day and change user history
    // if last activity was today, history will not change
    let date1 = Date.now();
    let date2 = habit.lastUpdate;
    let day1 = Math.floor(date1 / (1000 * 3600 * 24));
    let day2 = Math.floor(date2 / (1000 * 3600 * 24));
    if (day1 !== day2) {
      let frequency;
      daylyFrequency ? frequency = 'daylyHabits' : frequency = 'weeklyHabits';
      let differenseInDays = day1 - day2;

      let differenseInWeeks = Math.floor(differenseInDays / 7);
      let historyArr = Object.values(habit.history);

      if (daylyFrequency) {
        for (let i = 0; i < differenseInDays; i++) {
          historyArr.unshift(false);
        }
      }
      else {
        if (differenseInWeeks === 0) {
          return habit;
        }
        for (let i = 0; i < differenseInWeeks; i++) {
          historyArr.unshift(false);
        }
      }

      historyArr = historyArr.slice(0, 31);
      let historyStr = '{';
      for (let i = 0; i < 31; i++) {
        if (i === 30) historyStr += `"${i}": ${historyArr[i]}}`;
        else historyStr += `"${i}": ${historyArr[i]},`;
      }

      let habitData = {
        history: JSON.parse(historyStr),
        lastUpdate: Date.now(),
        name: habit.name
      }

      let updates = {};
      updates[`users/${this.state.uid}/${frequency}/${habit.name}/`] = habitData;
      database.ref().update(updates)
      return habitData;
    }
    return habit;
  }

  setUid = (uid, name) => {
    if (this.state.showRegistration) {
      this.setState({ uid: uid, showRegistration: !this.state.showRegistration });
      this.getUserData(uid);
    }
    else {
      this.setState({ uid: uid });
      this.getUserData(uid, true);
    }
  }

  setHabitName = ({ target: { value } }) => {
    const { newHabit } = this.state;
    newHabit.name = value.replace(/[^a-zA-Z0-9а-яА-Я ]/g, '');
    this.setState({ newHabit });
  }

  setHabitFrequency = ({ target }) => {
    let key = target.id.replace('frequency-', '');
    const { newHabit } = this.state;
    newHabit.frequency = key;
    this.setState({ newHabit });
  }

  showFormMessage = () => {
    document.querySelector('.form__message').classList.remove('hide');
    setTimeout(() => {
      document.querySelector('.form__message').classList.add('hide');
    }, 1500);
  }

  createHabit = async (event) => {
    event.preventDefault();
    if (!this.state.newHabit.name) {
      return this.showFormMessage();
    }
    let habitName = this.state.newHabit.name;
    let historyStr = '{';
    for (let i = 0; i < 31; i++) {
      if (i === 30) historyStr += `"${i}": false}`;
      else historyStr += `"${i}": false,`;
    }
    let habitData = {
      history: JSON.parse(historyStr),
      lastUpdate: Date.now(),
      name: habitName,
    }
    let frequency = this.state.newHabit.frequency;
    let updates = {}
    if (!this.state.userData.daylyHabits || !this.state.userData.weeklyHabits) {
      if (frequency === 'dayly' && !this.state.userData.daylyHabits) {
        updates[`users/${this.state.uid}/daylyHabits/${habitName}/`] = habitData;
        await database.ref().update(updates);
      }
      else if (frequency === 'weekly' && !this.state.userData.weeklyHabits) {
        updates[`users/${this.state.uid}/weeklyHabits/${habitName}/`] = habitData;
        await database.ref().update(updates);
      }
    }
    else if (frequency === 'dayly') {
      updates[`users/${this.state.uid}/daylyHabits/${habitName}/`] = habitData;
      await database.ref().update(updates);
    }
    else {
      updates[`users/${this.state.uid}/weeklyHabits/${habitName}/`] = habitData;
      await database.ref().update(updates);
    }
    document.querySelector('.habit-form').classList.add('hide');
    document.querySelector('.modal-wrap').classList.add('hide');
    this.setState({ newHabit: { name: '', frequency: 'dayly' } });
    this.getUserData(this.state.uid);
  }

  updateHabitBook = ({ target: { dataset } }) => {
    let updates = {};
    let name = dataset.name;
    let key = dataset.key;
    let { userData } = this.state;
    if (dataset.dayly === 'true') {
      let willChecked = !userData['daylyHabits'][`${name}`].history[`${key}`];
      userData['daylyHabits'][`${name}`].history[`${key}`] = willChecked;
      updates[`users/${this.state.uid}/daylyHabits/${name}/history/${key}`] = willChecked;
      if (key === '0') {
        updates[`users/${this.state.uid}/daylyHabits/${name}/lastUpdate`] = Date.now();
      }
    }
    else {
      let willChecked = !userData['weeklyHabits'][`${name}`].history[`${key}`];
      userData['weeklyHabits'][`${name}`].history[`${key}`] = willChecked;
      updates[`users/${this.state.uid}/weeklyHabits/${name}/history/${key}`] = willChecked;
      if (key === '0') {
        updates[`users/${this.state.uid}/weeklyHabits/${name}/lastUpdate`] = Date.now();
      }
    }
    this.setState({ userData: userData });
    database.ref().update(updates);
  }


  setDeleteHabit = ({ target: { dataset } }) => {
    document.querySelector('.modal-wrap').classList.remove('hide');
    document.querySelector('.modal-window').classList.remove('hide');
    this.setState({
      deleteHabit: {
        name: dataset.name,
        frequency: dataset.frequency
      }
    })
  }

  deleteHabit = async () => {
    let name = this.state.deleteHabit.name;
    if (this.state.deleteHabit.frequency === 'dayly') {
      await database.ref(`users/${this.state.uid}/daylyHabits/${name}/`).remove();
    }
    else {
      await database.ref(`users/${this.state.uid}/weeklyHabits/${name}/`).remove();
    }
    document.querySelector('.modal-wrap').classList.add('hide');
    document.querySelector('.modal-window').classList.add('hide');
    this.getUserData(this.state.uid);
  }

  render() {
    return ((this.state.signIn && !this.state.uid) || this.state.showRegistration) ? (<Authorisation setUid={this.setUid} state={this.state} signUp={this.signUp} />) :
      (
        <div className="App">
          <Header signIn={this.signIn} state={this.state} setHabitFrequency={this.setHabitFrequency} setHabitName={this.setHabitName} createHabit={this.createHabit} deleteHabit={this.deleteHabit} setDeleteHabit={this.setDeleteHabit} updateHabitBook={this.updateHabitBook} />
          <Footer signIn={this.signIn} state={this.state} signUp={this.signUp} />
        </div>
      )
  };
}


export default App;
